




















import Vue from 'vue';

export default Vue.extend({
  name: 'Header',
  data() {
    return {
      movieSearch: '',
      drawer: null,
    };
  },
  components: {
    NavigationItems: () => import('@/components/header/NavigationItems.vue'),
  },
  methods: {
    findMovie(): void {
      this.$router.push({
        name: 'movies',
        params: { filtered_by: 'search' },
        query: { query: this.movieSearch, page: '1' },
      });
    },
  },
});
